@import '../../styles/constants';

.mainBg {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 95vh;
  padding-top: 50px;

  .mainImg {
    margin-left: 20px;
    overflow: hidden;
    img {
      height: 680px;
    }

    .locatorContainer {
      position: absolute;
      z-index: 1;
      .locator {
        img {
          width: 80px;
          height: 95px;
        }
      }
    }

  }

  .mainText {
    margin-left: 150px;
    width: 660px;

    h1 {
      font-size: 50px;
      font-family: 'Unbounded', cursive;
    }

    p {
      margin: 20px 0 60px;
      font-size: 20px;
      color: $text-color-add-comment;
      font-weight: 600;
    }

    .mainButtons {
      a {
        width: 160px;
      }

      a:first-child {
        margin-right: 16px;
        //background: $main-blue;
      }
    }
  }
}


