@media (max-width: 850px) {
  .mainBg {
    display: block;

    .mainImg {
      img {
        //width: 96vw;
        //height: auto;
        height: 45vh;
        max-width: 100vw;
        margin-top: 30px;
        float: right;
      }

      .locatorContainer {
        display: none;
      }
    }

    .mainText {
      margin-left: 14px!important;
      position: absolute;
      bottom: 80px;
      width: 92vw!important;

      h1 {
        font-size: 32px!important;
        max-width: 100vw;
      }

      p {
        font-size: 16px;
        margin: 20px 0 40px;
      }
    }
  }
}

@media (max-width: 425px) {

  .mainBg {
    display: block;

    .mainImg {
      img {
        width: 96vw;
        float: left;
        height: auto;
        //height: 35vh;
        //width: auto;
        margin-top: 30px;
      }

      .locatorContainer {
        display: none;
      }
    }

    .mainText {
      margin-left: 14px!important;
      position: absolute;
      bottom: 80px;
      width: 92vw!important;

      h1 {
        font-size: 32px!important;
        max-width: 100vw;
      }

      p {
        font-size: 16px;
        margin: 20px 0 40px;
      }
    }
  }


}
